import empty from "@/components/empty";
import { sendAjax } from '@/common/js/sendAjax.js';
import { templeMsg } from '@/common/js/templeMsg.js';
import config from '@/apiConfig';
const { getFormId,getOrderList,updateOrderState} = config.api;
export default {
	components: {
		empty
	},
	data() {
		return {
			role:1, //模拟身份，1为用户，2为保洁员
			tabCurrentIndex: 0,
			navList: [{state: 0,text: '全部'},
				// {state: 1,text: '待预约'},
				{state: 1,text: '预约中'},
				{state:2,text: '已预约'},
				{state: 3,text: '已失败'},
				{state: 4,text: '已关闭'}
				// {state: 4,text: '服务中'},
				// {state: 5,text: '待评价'}
			],
			orderList:[],
			orderState:0,
			pageNum: 1,
			pageSize: 5
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = +options.state||0;
		this.getOrderList();
	},
	 
	methods: {
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
			this.orderState = index;
			this.getOrderList();
		},
		//普通用户取消订单
		userCancelOrder(e){
			var that = this;
			var detail = e.currentTarget.dataset.detail;
			var id = e.currentTarget.dataset.id;
			var name = detail.employerName;
			var phone = detail.employerPhone;
			var address = detail.address;
			var time = detail.startTime;
			var reason = '用户取消预约';
			var cleanerId = detail.clearnerId;
			uni.showModal({
			    title: '提示',
			    content: '确认取消预约？',
			    success: function (res) {
			        if (res.confirm) {
			           var data = {
			           		 id:id,
			           		 orderState:3
			           	}
			           let infoOpt = {
			           	url: updateOrderState,
			           	type: 'PUT',
			           	data: JSON.stringify(data),
			           };
			           let infoCb = {};
			           infoCb.success = function(res) {
			           	console.log(res)
			           	if(res == null){
							  var template_id = 'Z3NPw5AOxC8Dt4-qETy-a2aQrEaQ02Dfy3iQdm-mleE';
							  var page = '/pages/order/order';
							  var data = {
								"keyword1": {
								  "value": id
								},
								"keyword2": {
								  "value": name
								},
								"keyword3": {
								  "value": phone
								},
								"keyword4": {
								  "value": address
								},
								"keyword5": {
								  "value": time
								},
								"keyword5": {
								  "value": reason
								}
								
							  };
							  templeMsg(cleanerId,template_id, page, data);
			           		uni.showToast({
			           			title:'取消成功'
			           		})
							that.getOrderList();
			           	}
			           	uni.hideLoading()
			           },
			           infoCb.beforeSend = () => {
			             uni.showLoading({
			             	title:'加载中',
							icon:'none'
			             })
			           };
			           sendAjax(infoOpt, infoCb,1);
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		//保洁员取消订单
		cleanerCancelOrder(e){
			var that = this;
			var detail = e.currentTarget.dataset.detail;
			var id = e.currentTarget.dataset.id;
			var name = detail.employerName;
			var phone = detail.employerPhone;
			var address = detail.address;
			var time = detail.startTime;
			var reason = '保洁员取消预约';
			var cleanerName = detail.cleaner.name;
			var userOpenId = detail.employerId;
			uni.showModal({
			    title: '提示',
			    content: '确认取消预约？',
			    success: function (res) {
			        if (res.confirm) {
			           var data = {
			           		 id:id,
			           		 orderState:3
			           	}
			           let infoOpt = {
			           	url: updateOrderState,
			           	type: 'PUT',
			           	data: JSON.stringify(data),
			           };
			           let infoCb = {};
			           infoCb.success = function(res) {
			           	console.log(res)
			           	if(res == null){
							  var template_id = 'eWomX6rSFV-hnNJ_cQBC24CskcMUUs4uCuA_CamgjWg';
							  var page = '/pages/order/order';
							  var data = {
								"keyword1": {
								  "value": cleanerName
								},
								"keyword2": {
								  "value": name
								},
								"keyword3": {
								  "value": phone
								},
								"keyword4": {
								  "value": address
								},
								"keyword5": {
								  "value": time
								},
								"keyword6": {
								  "value": reason
								}
								
							  };
							  templeMsg(userOpenId,template_id, page, data);
			           		uni.showToast({
			           			title:'取消成功'
			           		})
							that.getOrderList();
			           	}
			           	uni.hideLoading()
			           },
			           infoCb.beforeSend = () => {
			             uni.showLoading({
			             	title:'加载中',
							icon:'none'
			             })
			           };
			           sendAjax(infoOpt, infoCb,1);
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		//保洁员确认订单
		confirmOrder(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			var detail = e.currentTarget.dataset.detail;
			var name = detail.employerName;
			var phone = detail.employerPhone;
			var address = detail.address;
			var time = detail.startTime;
			var userOpenId = detail.employerId;
			uni.showModal({
			    title: '提示',
			    content: '确认预约？',
			    success: function (res) {
			        if (res.confirm) {
			           var data = {
			           		 id:id,
			           		 orderState:2
			           	}
			           let infoOpt = {
			           	url: updateOrderState,
			           	type: 'PUT',
			           	data: JSON.stringify(data),
			           };
			           let infoCb = {};
			           infoCb.success = function(res) {
			           	console.log(res)
			           	if(res == null){
							var template_id = 'mPkWwY2fUYwJLcwpnJk9Sr7PdIPkUtmNnN9aq9iU5-4';
							var page = '/pages/order/order';
							var data = {
									"keyword1": {
									  "value": id
									},
									"keyword2": {
									  "value": name
									},
									"keyword3": {
									  "value": phone
									},
									"keyword4": {
									  "value": address
									},
									"keyword5": {
									  "value": time
									}
																
							};
							templeMsg(userOpenId,template_id, page, data);
			           		uni.showToast({
			           			title:'确认成功'
			           		})
							that.getOrderList();
			           	}
			           	uni.hideLoading()
			           },
			           infoCb.beforeSend = () => {
			             uni.showLoading({
			             	title:'加载中',
							icon:'none'
			             })
			           };
			           sendAjax(infoOpt, infoCb,1);
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		//获取订单列表
		getOrderList(){
			var pageNum = this.pageNum;
			var pageSize = this.pageSize;
			var employerId = uni.getStorageSync('userInfo').openId;
			var orderState = this.orderState;
			var that = this;
			if(this.orderState == 0){
				var data = {
					employerId:employerId,
					pageNum: pageNum,
					pageSize: pageSize
				}
			}else{
				var data = {
					employerId:employerId,
					orderState:orderState,
					pageNum: pageNum,
					pageSize: pageSize
				}
			}
			
			let infoOpt = {
				url: getOrderList,
				type: 'GET',
				data:data
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.orderList = res.list;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		toDetail(e){
			var id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url:'orderDetail/orderDetail?id='+id
			})
		},
		//收集formId
		  getFormId:function(e){
			  console.log(e)
		    var formId = e.detail.formId;
		    // var userId = wx.getStorageSync('userinfo').userId;
		    var openId = uni.getStorageSync('userInfo').openId;
		    console.log(openId)
		    if (formId != 'the formId is a mock one'){
		      var that = this;
		      let infoOpt = {
		        url:getFormId,
		        type: 'POST',
		        data: {
		          // userId: userId,
		          openId: openId,
		          formId: formId
		        },
		        header: {
		          'content-type': 'application/json',
		        },
		      }
		      let infoCb = {}
		      infoCb.success = function (res) {
				  console.log(res)
		      }
		      infoCb.beforeSend = () => { }
		      sendAjax(infoOpt, infoCb, () => { });
		    }
		    
		  }
		
	},
}
